function mostrarResumen() {
    // Destruturing
    const { nombre, fecha, hora, servicios } = cita;

    // Seleccionar el resumen del html
    const resumenDiv = document.querySelector('#paso-3');

    // Limpiar el html previo
    resumenDiv.innerHTML = '';

    // Seleccionar para despues añadir contenido
    const form = document.querySelector('#paso-3');
    // Validación de objeto

    const hedingCita = document.createElement('H3');
    hedingCita.textContent = 'Resumen Cita';
    
    const nombreCita = document.createElement('P');
    nombreCita.innerHTML = `<span>Nombre:</span> ${nombre}`;

    const fechaCita = document.createElement('P');
    fechaCita.innerHTML = `<span>Fecha:</span> ${fecha}`;

    const horaCita = document.createElement('P');
    horaCita.innerHTML = `<span>Hora:</span> ${hora}`;

    let precioTotal = 0;

    const serviciosCita = document.createElement('DIV');
    serviciosCita.classList.add('resumen-servicios');

    const hedingServicios = document.createElement('H3');
    hedingServicios.textContent = 'Resumen de Serivicios';

    
    serviciosCita.appendChild(hedingServicios);

    servicios.forEach( servicio => {
        const { precio, nombre } = servicio;

        precioTotal += parseInt(servicio.precio.split('€')[0]);

        const contenedorServicio = document.createElement('DIV');
        contenedorServicio.classList.add('contenedor-servicio');
        
        const textoServicio = document.createElement('P');
        textoServicio.textContent = nombre;
        textoServicio.classList.add('nombre');

        const precioServicio = document.createElement('P');
        precioServicio.textContent = precio;
        precioServicio.classList.add('precio');

        // Colocar Texto y precio en el div
        contenedorServicio.appendChild(textoServicio);
        contenedorServicio.appendChild(precioServicio);
        serviciosCita.appendChild(contenedorServicio);

    });

    const precioTotalHtml = document.createElement('P');
    precioTotalHtml.classList.add('total');
    precioTotalHtml.innerHTML = `<span>Total a Pagar: </span>${precioTotal}€`;
    
    serviciosCita.appendChild(precioTotalHtml);

    resumenDiv.appendChild(hedingCita);
    resumenDiv.appendChild(nombreCita);
    resumenDiv.appendChild(fechaCita);
    resumenDiv.appendChild(horaCita);


    resumenDiv.appendChild(serviciosCita);

};

function nombreCita() {
    const nombreInput = document.querySelector('#nombre');
    nombreInput.addEventListener('input', (e) => {
        const nombreTexto = e.target.value.trim();

        // Validad de que tengo algo el nombreTexto
        if( nombreTexto === '' || nombreTexto.length < 4) {
            mostrarAlerta('El nombre debe de tener minimo 4 letras', 'error', '.form');
        } else {
            const alerta = document.querySelector('.alerta');
            if(alerta){
                alerta.remove();
            };
            cita.nombre = nombreTexto;
        }
        
    })
};

function mostrarAlerta(mensaje, tipo, donde) {
    const alertaPrevia = document.querySelector('.alerta');
    if(alertaPrevia) {
        return;
    }

    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');

    if(tipo === 'error') {
        alerta.classList.add('error');
    }

    // Insertar en el html
    const formularioDiv = document.querySelector(donde);
    formularioDiv.appendChild(alerta);

    // Eliminar la alerta
    setTimeout(() => {
        alerta.remove();
    },3000);
};

function fechaCita() {
    const fechaInput = document.querySelector('#fecha');
    fechaInput.addEventListener('change', e => {
        

        const dia = new Date(e.target.value).getUTCDay();
        
        if( [0].includes(dia)) {
            e.preventDefault();
            fechaInput.value = '';
            mostrarAlerta('Fines de semana no abrimos', 'error', '.form')
        } else {
            cita.fecha = fechaInput.value;
        }
        // console.log(dia.toLocaleDateString('es-Es',opciones));
    })
};

function horaCita() {
    const inputHora = document.querySelector('#hora');

    inputHora.addEventListener('change', e => {
        e.preventDefault();
        
        const horaCita = e.target.value;
        const hora = horaCita.split(':')[0];
        
        if (hora < 09 || hora > 21){
            mostrarAlerta('Hora no válida', 'error', '.form');
            setTimeout(() =>{
                inputHora.value = '';
            }, 1000);
            return
        }
        cita.hora = inputHora.value;
    })
};

function deshabilitarFechaAnterior() {
    const inputFecha = document.querySelector('#fecha');

    const fechaAhora = new Date();
    const year = fechaAhora.getFullYear();
    const mes = fechaAhora.getMonth() + 1;
    const dia = fechaAhora.getDate() + 1;

    var mostrarDia, mostrarMes;
    // Formato deseado AAAA-MM-DD
    mes < 10 ? mostrarMes = `0${mes}`: mostrarMes = mes;
    dia < 10 ? mostrarDia = `0${dia}`: mostrarDia = dia;
    
    const fechaDeshabilitar = `${year}-${mostrarMes}-${mostrarDia}`;
    inputFecha.min = fechaDeshabilitar;
}