let pagina = 1;

const cita = {
    nombre: '',
    fecha: '',
    hora: '',
    servicios: []
};
document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
});

function iniciarApp() {
    mostrarServicios();

    // Resalta el Div Actual segun el tab que se presiona

    // Oculta o muestra una seccion segun el tab al que se presiona
    cambiarSeccion();

    // Paginación siguiente y anterios
    paginaSiguiente();

    paginaAnterior();
    
    // Comprueba Pagina actual para mostrar o ocultar
    compruebaPagina();

    // Muestra el mensaje de la cita o muestra un error
    mostrarResumen();

    // Guardar nombre cita
    nombreCita();

    // Guardar fecha cita
    fechaCita();

    // Desabilitar fecha anterior
    deshabilitarFechaAnterior();

    // Guardar hora cita
    horaCita();
};

function mostrarSeccion() {
    
    // Constantes para eliminar
    const seccionAnterior = document.querySelector('.mostrar-seccion');
    const botonEstasEn = document.querySelector('.estas-en');
    if (seccionAnterior) {
        document.querySelector('.mostrar-seccion').classList.remove('mostrar-seccion');
    };
    if (botonEstasEn) {
        document.querySelector('.estas-en').classList.remove('estas-en');
    };

    // Seccion actual
    const seccionActual = document.querySelector(`#paso-${pagina}`);
    seccionActual.classList.add('mostrar-seccion');

    const tab = document.querySelector(`#button-${pagina}`);
    tab.classList.add('estas-en');

};
function cambiarSeccion() {
    const enlaces = document.querySelectorAll('.tabs button');
    
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', e => {
            e.preventDefault();
            pagina = parseInt(e.target.dataset.paso);
            
            
            // Comprobar disponibilidad
            compruebaPagina();
        })
    })
};




