async function mostrarServicios() {
    try {
        const resultado = await fetch('./servicios.json');
        const db = await resultado.json();
        const { servicios } = db;
        servicios.forEach(element => {
            const { id, nombre, precio } = element;

            // DOM Scripting
            // Genera nombre del Servicio
            const nombreServivio = document.createElement('P');
            nombreServivio.textContent = nombre;
            nombreServivio.classList.add('nombre-servicio');

            // Genenerar precio del Servicio
            const precioServivio = document.createElement('P');
            precioServivio.textContent = `${precio}€`;
            precioServivio.classList.add('precio-servicio');

            // Generar el div del contenedor del servicio
            const servicioDiv = document.createElement('DIV');
            servicioDiv.classList.add('servicio');
            servicioDiv.dataset.idServicio = id;

            // Selecciona un servicio para la cita
            servicioDiv.onclick = seleccionarServicio;
            
            // Inyectar el precio y nombre al servicio
            servicioDiv.appendChild(nombreServivio);
            servicioDiv.appendChild(precioServivio);
            
            // Inyectar en el html
            const serviciosHtml = document.querySelector('#servicios');
            serviciosHtml.appendChild(servicioDiv);
        });
    }catch(error) {
        console.log(error);
    };
};

function seleccionarServicio(e){
    // Forzar el elemento al cual le damos click al div

    let elemento;
    if(e.target.tagName === 'P') {
        elemento = e.target.parentElement;
    }else {
        elemento = e.target;
    }
    if(elemento.classList.contains('seleccionado')) {
        elemento.classList.remove('seleccionado')

        const id = parseInt(elemento.dataset.idServicio);

        eliminarServicio(id);
    } else {
        elemento.classList.add('seleccionado');

        const nombre = elemento.firstElementChild.textContent;
        const precio = elemento.firstElementChild.nextElementSibling.textContent;
        const serviciObj = {
            id: parseInt(elemento.dataset.idServicio),
            nombre: nombre,
            precio: precio
        };
        agregarServicio(serviciObj);
    };
};

function agregarServicio(servicioObj) {
    const { servicios } = cita;
    cita.servicios = [...servicios, servicioObj];
};

function eliminarServicio(id) {
    const { servicios } = cita;

    cita.servicios = servicios.filter(servicio => servicio.id !== id);
};

function paginaAnterior() {
    const paginaAnterior = document.querySelector('#anterior');
    paginaAnterior.addEventListener('click', ()=> {
        pagina--;
        compruebaPagina();
    });
};

function paginaSiguiente() {
    const paginaSiguiente = document.querySelector('#siguiente');
    paginaSiguiente.addEventListener('click', () => {
        pagina++;
        compruebaPagina();
    })
};

function compruebaPagina() {
    const paginaSiguiente = document.querySelector('#siguiente');
    const paginaAnterior = document.querySelector('#anterior');
    
    if (pagina === 1 || pagina === 3) {
        if (pagina === 1){
            paginaAnterior.classList.add('ocultar');
            paginaSiguiente.classList.remove('ocultar');
        } else {
            paginaSiguiente.classList.add('ocultar');
            paginaAnterior.classList.remove('ocultar');
            mostrarResumen();
        }

    } else if (pagina === 2) {
        paginaAnterior.classList.remove('ocultar');
        paginaSiguiente.classList.remove('ocultar');
    }
    mostrarSeccion();
    
};